import { createApp } from "vue";
import ViewDetailsDialog from '../components/Tests/ViewDetailsDialog.vue'

export function TestViewDialog (show: boolean | undefined, test: any) : void {

    const rootContainer = document.getElementById('rootContainer')!;

    const app = createApp(ViewDetailsDialog, {
        show: show,
        test: test
    });
    app.mount(rootContainer, false )

}
