<template>
    <ion-page>
        <div id="rootContainer"></div>
        <ion-header>
            <ion-toolbar>
                <ion-title class="his-md-text">
                    {{ title }}
                    <br v-if="subtitle" />
                    <span class="his-sm-text">{{ subtitle }}</span>
                </ion-title>
                <ion-buttons slot="end">
                    <ion-button v-if="typeof onConfigure === 'function'" @click="() => onConfigure && onConfigure()">
                        <ion-icon size="large" :icon="document"></ion-icon>
                    </ion-button>
                    <ion-chip v-if="searchTerm" @click="clearSearch" class="his-md-text" color="warning">
                        <ion-label>{{ searchTerm }}</ion-label>
                        <ion-icon :icon="close"></ion-icon>
                    </ion-chip>
                    <ion-button @click="searchTable" size="large">
                        <ion-icon size="large" :icon="search"></ion-icon>
                    </ion-button>
                    <ion-button v-if="typeof onRefresh === 'function'" @click="() => onRefresh && onRefresh()"
                        color="success" size="large">
                        <ion-icon size="large" :icon="sync"></ion-icon>
                    </ion-button>
                </ion-buttons>
            </ion-toolbar>
        </ion-header>
        <ion-content scroll-y scroll-x>
            <table class="art-report-theme">
                <thead class="stick-report-header">
                    <tr v-for="(column, index) in columns" :key="index">
                        <th v-for="(item, i) in column" :key="i" :colspan="item?.span?.thColspan || 1"
                            :rowspan="item?.span?.thRowspan || 1" @click="sortColumn(item.ref)">
                            <ion-item lines="none">
                                <ion-icon v-if="columnSorted && columnSorted === item.ref"
                                    :icon="sortOrder === 'asc' ? arrowUp : arrowDown">
                                </ion-icon>
                                <ion-label class="ion-text-center uppercase">
                                    <b style="font-size: 19px;">{{ item.label }}</b>
                                </ion-label>
                            </ion-item>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(data, i) in page" :key="i">
                        <td v-for="(info, k) in data.row" :key="k" @click="() => onClickTablecell(data)" :class="{
                            'clickable-cell': info?.column?.tdClick
                        }">
                            <span class="his-sm-text">
                                {{ k == 0 ? (moment(info.value).isValid() ? moment(info.value).format('DD/MMMM/YYYY HH:mm') : info.value) : info.value }}
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </ion-content>
        <ion-footer>
            <ion-toolbar color="light">
                <ion-button v-if="totalPages > 1" @click="first" :disabled="currentPage === 1" size="large" slot="end">
                    First
                </ion-button>
                <ion-button v-if="totalPages > 1" @click="prev" :disabled="!canPrev" color="light" size="large"
                    slot="end">
                    Prev
                </ion-button>
                <ion-button v-if="totalPages > 1" @click="next" :disabled="!canNext" color="light" size="large"
                    slot="end">
                    Next
                </ion-button>
                <ion-button @click="finish" color="success" size="large" slot="end">
                    Finish
                </ion-button>
            </ion-toolbar>
        </ion-footer>
    </ion-page>
</template>
<script lang="ts">

import moment from "moment"
import {
    IonPage,
    IonLabel,
    IonIcon,
    IonItem,
    IonTitle,
    IonChip,
    IonToolbar,
    IonHeader,
    IonButton,
    IonFooter,
    IonContent,
    IonButtons
} from "@ionic/vue"
import {
    sync,
    search,
    close,
    arrowUp,
    arrowDown,
    document
} from "ionicons/icons"
import { Option } from "@/components/Forms/FieldInterface"
import { computed, defineComponent, PropType, ref, watch } from "vue";
import keyboard from "@/utils/PopupKeyboard"
import { chunk, isEmpty } from "lodash";
import { sort } from "fast-sort";
import { numericKeypad } from "@/utils/PopupKeyboard";
import { FieldType } from "@/components/Forms/BaseFormElements";
import Fuse from "fuse.js"
import { v2ColumnDataInterface, v2ColumnInterface } from "./types";
import { useRouter } from "vue-router";
import { TestViewDialog } from "@/utils/TestViewDialog";
import { defineEmits } from "vue";

export default defineComponent({
    components: {
        IonPage,
        IonToolbar,
        IonItem,
        IonIcon,
        IonLabel,
        IonTitle,
        IonHeader,
        IonButton,
        IonFooter,
        IonContent,
        IonButtons,
        IonChip
    },
    props: {
        title: {
            type: String,
            default: 'Report'
        },
        subtitle: {
            type: String,
        },
        columns: {
            type: Object as PropType<Array<v2ColumnInterface[]>>,
            required: true
        },
        columnData: {
            type: Array,
            default: () => []
        },
        enablePagination: {
            type: Boolean,
            default: true
        },
        rowsPerPage: {
            type: Number,
            default: 10
        },
        onFinish: {
            type: Function
        },
        onRefresh: {
            type: Function
        },
        onConfigure: {
            type: Function
        },
        csvQuarter: {
            type: String
        },
        searchValue: {
            type: String
        },
        pageMetadata: {
            type: Object as PropType<{
                current_page: number;
                next_page: number;
                prev_page: number;
                total_pages: number;
                total_count: number;
            }>,
            default: () => ({
                current_page: 1,
                next_page: 1,
                prev_page: 1,
                total_pages: 1,
                total_count: 1
            })
        }
    },
    data() {
        return {
            moment: moment
        }
    },
    setup(props, { emit }) {
        const route = useRouter()
        const reportData = ref<any>([])
        const currentPage = ref<number>(1)
        const searchTerm = ref<string>('')
        const columnSorted = ref<string>('')
        const sortOrder = ref<'asc' | 'desc'>('desc')
        /**
         * This computed object exist to allow for transformation
         * of "reportData" without mutations when doing
         * things like search or sorting...
         */
        const pageComputedReportData = computed(() => {
            /**
             * Data transformations for table happen here. i.e. searching or sorting
            */
            const tableData = sort(reportData.value)[sortOrder.value]((d: any) => d.data[columnSorted.value])

            if (searchTerm.value) {
                const fuse = new Fuse(tableData, {
                    threshold: 0.3,
                    keys: ['searchIndex'],
                    useExtendedSearch: true
                })
                return fuse.search(searchTerm.value, { limit: props.rowsPerPage }).map((i: any) => i.item)
            }
            return tableData
        })

        const totalPages = computed(() => props.pageMetadata.total_count)
        const canNext = computed(() => props.pageMetadata.current_page < props.pageMetadata.next_page)
        const canPrev = computed(() => props.pageMetadata.prev_page > 0)
        /**
         * returns current page data based on page index
         */
        const page = computed(() => {
            /**
             * Paginated data that ends up on the template.
             */
            // const paginated = chunk(pageComputedReportData.value, props.rowsPerPage)
            return pageComputedReportData.value as any
        })

        const sortColumn = (column: string) => {
            columnSorted.value = column
            sortOrder.value = sortOrder.value === 'asc' ? 'desc' : 'asc'
        }
        /**
         * Launches numeric input to allow the user to skip to any page
         */
        const selectPage = () => numericKeypad((v: string) => {
            const num = parseInt(v)
            if (parseInt(v) <= totalPages.value) currentPage.value = num
        }, { title: 'Select page' })
        /**
         * Launches search input to filter page
         */
        const searchTable = () => {
            keyboard({
                id: "search",
                helpText: "Find",
                type: FieldType.TT_TEXT,
            },
                (v: Option) => {
                    // This has been wrapped in a slight delay to improve responsiveness of the page
                    setTimeout(() => {
                        searchTerm.value = v ? v.value as string : ''
                        currentPage.value = 1
                        emitSearch(searchTerm.value, 1)
                    }, 3)
                })
        }

        const emitSearch = (value: string, page: number) => {
            emit('search', value, page)
        }
        /**
         * Reset current page to initial
         */
        const first = () => {
            currentPage.value = 1
            emitSearch(searchTerm.value, currentPage.value)
        }
        /**
         * Goes to the last page
         */
        const last = () => currentPage.value = (totalPages.value - 1) || 0
        /**
         * Go to next page relative to the current page
         */
        const next = () => {
            currentPage.value += 1
            emitSearch(searchTerm.value, currentPage.value)
        }
        /**
         * Go to previous page relateive to the current page
         */
        const prev = () => {
            currentPage.value -= 1
            emitSearch(searchTerm.value, currentPage.value)
        }

        const finish = () => {
            if (typeof props.onFinish === 'function') {
                props.onFinish()
            } else {
                route.push('/')
            }
        }

        /**
         * Event handler when TD on HTML report is clicked
         * @param cell
         */
        const onClickTablecell = (cell: any) => {

            TestViewDialog(true, cell.data)

            try {
                if (typeof cell.column?.tdClick === 'function') {
                    cell.column?.tdClick(cell)
                }
                return
            } catch (e) {
                console.error(e)
            }
        }

        const getExpandedColumns = () => props.columns.reduce((a: any, c: v2ColumnInterface[]) => a.concat(c), [])
            .filter((col: v2ColumnInterface) => col.value || col.ref)

        /**
         * Create a table header and cell mapping
         * @param data
         */
        const mapcolumnValues = (data: any) => {
            data.forEach((record: any) => {
                const row = getExpandedColumns().map((column: v2ColumnInterface) => {
                    let value = ''
                    try {
                        if (isEmpty(record)) {
                            value = "..."
                        } else if (typeof column.value === 'function') {
                            value = column.value(record) as string
                        } else {
                            // Use the ref to map to a value inside the record
                            value = record[column.ref] || ''
                        }
                    } catch (e) {
                        value = '_ERROR_'
                        console.error(e)
                    }
                    return {
                        column,
                        data: record,
                        value: value,
                        [column.ref || 'nada']: value
                    }
                })
                reportData.value.push({
                    row,
                    data: record,
                    searchIndex: row.map((d: any) => `${d.value}`)
                })
            })
        }

        /**
         * Initiates generation of table data through column data prop
         */
        watch(() => props.columnData, (data) => {
            reportData.value = [];
            if (!Array.isArray(data) || !data.length) {
                currentPage.value = 1
                searchTerm.value = ''
                columnSorted.value = ''
                mapcolumnValues(Array.from({ length: props.rowsPerPage }).map(() => ({})))
                return
            }
            mapcolumnValues(data)
        }, { immediate: true, deep: true })

        const clearSearch = () => {
            searchTerm.value = '';
            emit('search', '', 1)
        }

        return {
            clearSearch,
            first,
            last,
            next,
            prev,
            selectPage,
            sortColumn,
            searchTable,
            onClickTablecell,
            finish,
            sync,
            search,
            close,
            arrowUp,
            arrowDown,
            document,
            page,
            canNext,
            searchTerm,
            totalPages,
            sortOrder,
            canPrev,
            currentPage,
            columnSorted,
        }
    }
})
</script>
<style scoped>
th,
td {
    width: 4%;
    border-collapse: collapse;
    text-align: center;
}

td {
    border-bottom: 1px solid rgb(165, 165, 165);
    padding: 0.6em;
    font-weight: 500;
    font-size: .8em;
}

.stick-report-header {
    background-color: #969696 !important;
    border-bottom: 2px solid #ccc;
    position: sticky;
    top: 0;
    z-index: 1000;
}

tr:nth-child(even) {
    background-color: #ececec;
}

.clickable-cell {
    color: #0645AD;
    font-weight: 600;
    font-size: 1em;
    cursor: pointer;
}

table.art-report-theme thead th {
    min-width: 8vw;
    background: #ffffff;
    color: var(--his-font-color);
    border: 1px solid rgb(224, 224, 224);
    padding: 0.2em;
    font-size: 1.1rem;
    border-bottom: 2px solid #3333;
}

table.art-report-theme tbody td {
    border: 1px solid rgb(224, 224, 224);
    min-height: 5vh;
}

.uppercase {
    text-transform: uppercase;
}
</style>
