<template>
    <div>
        <poc-table :searchValue="searchValue" title="All Tests" @search="search" :columns="columns" :columnData="rows" :rowsPerPage="25" :finish="onFinish" :onRefresh="updateRows" :pageMetadata="pageMetadata">
        </poc-table>
        <div id="rootContainer"></div>
    </div>
</template>
<script setup lang="ts">
import PocTable from "@/components/DataViews/tables/v2PocDatatable/TableView.vue"
import { onMounted, ref } from "vue"
import { Client } from "@/composables/client"
import { toastWarning } from "@/utils/Alerts"
import router from "@/router"
import { LabLocation } from "types"

const columns: any = [
    [
        {
            label: "Date ordered",
            ref: "created_date"
        },
        {
            label: 'Patient No',
            value: (data: any) => data?.client?.id || '-'
        },
        {
            label: 'Patient Name',
            value: (data: any) => `${data?.client?.first_name} ${data?.client?.last_name}`
        },
        {
            label: 'Accession No',
            ref: "accession_number"
        },
        {
            label: 'Test type',
            ref: "test_type_name"
        },
        {
            label: 'Test Location',
            ref: "requesting_ward"
        },
        {
            label: 'Test status',
            ref: "status"
        }
    ]
]

const rows = ref<any[]>([])

const pageMetadata = ref({
  current_page: 1,
  next_page: 1,
  prev_page: 1,
  total_pages: 1,
  total_count: 1
});

const client = Client()

const searchValue =  ref<string>(`${router.currentRoute.value.query.patient_id !== undefined ? router.currentRoute.value.query.patient_id : '' }`)

function onFinish() {
    router.push('/')
}

const search = (value: string, page: number) => {
    updateRows(value, page)
}

function updateRows(search: string, page: number) {
    const value = search ? search : "";
    const pageValue = page ? page : 1
    const { getLocation, token } = Client();
    const data = token.value?.authorization?.user;
    const labLocationID = data.lab_locations.filter((location: LabLocation) => location.name == getLocation())
    client.getJson(`tests?search=${value}&page=${pageValue}&per_page=24&lab_location=${labLocationID[0].id}`)
        .then((res: any) => {
            if (!res.ok) return toastWarning(res.error)
            rows.value = res.data.data.reverse()
            pageMetadata.value = res.data.meta
        })
}

onMounted(() => updateRows("", 1))
</script>
