<template>
  <ion-page>
    <ion-content class="ion-padding">
      <ion-modal :is-open="open" style="--width: 70%;">
        <ion-header>
          <ion-toolbar>
            <ion-title style="font-size: 20px; font-weight: 600"
              >Test Details</ion-title
            >
            <ion-buttons slot="end">
              <ion-button :strong="true" @click="handleDialog">
                <IonIcon size="large" :icon="close"></IonIcon>
              </ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-list>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Tracking Number</ion-label
              >
              <ion-label slot="end">{{ details?.tracking_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Accession Number</ion-label
              >
              <ion-label slot="end">{{ details?.accession_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Patient Name</ion-label
              >
              <ion-label slot="end">{{
                `${details?.client.first_name} ${
                  details?.client.middle_name !== null
                    ? details?.client.middle_name
                    : ""
                }
                ${details?.client.last_name}`
              }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Type</ion-label
              >
              <ion-label slot="end">{{ details?.specimen_type }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Test(s) Ordered</ion-label
              >
              <ion-label slot="end">{{ details?.test_type_name }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Status</ion-label
              >
              <ion-label slot="end">{{
                details?.order_status == "specimen-not-collected"
                  ? "Specimen not collected"
                  : details?.order_status
              }}</ion-label>
            </ion-item>
          </ion-list>
        </ion-content>
        <ion-footer>
          <ion-toolbar color="light" style="padding: 5px">
            <ion-button
              slot="end"
              size="large"
              color="primary"
              @click="viewTestDetails"
            >
              View
            </ion-button>
            <ion-button size="large" slot="end" @click="printAccessNumber">
              Print Accession Number
            </ion-button>
            <ion-button
              v-if="
                details?.order_status !== 'specimen-accepted' &&
                details?.order_status !== 'specimen-rejected' &&
                details.status &&
                !details?.status.includes('rejected')
              "
              @click="changeSpecimenStatus('accepted')"
              slot="end"
              size="large"
              color="success"
            >
              Accept
            </ion-button>
            <ion-button
              v-if="
                details?.order_status !== 'specimen-accepted' &&
                details?.order_status !== 'specimen-rejected' &&
                details.status &&
                !details?.status.includes('rejected')
              "
              @click="rejectOrder"
              slot="end"
              size="large"
              color="danger"
            >
              Reject
            </ion-button>
            <ion-button
              @click="addTest"
              v-if="details?.order_status !== 'specimen-rejected'"
              slot="end"
              size="large"
              color="primary"
            >
              Add Test
            </ion-button>
          </ion-toolbar>
        </ion-footer>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script lang="ts">
import { Client } from "@/composables/client";
import router from "@/router";
import { toastSuccess, toastWarning } from "@/utils/Alerts";
import { MultiStepPopupForm } from "@/utils/PopupKeyboard";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonModal,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
  modalController,
} from "@ionic/vue";

import { close } from "ionicons/icons";

import { PropType } from "vue";

import { defineComponent } from "vue";
import { FieldType } from "../Forms/BaseFormElements";

import Orders from "@/composables/orders";

import Validation from "@/components/Forms/validations/StandardValidations";
import { Option } from "../Forms/FieldInterface";
import { RejectDialog } from "@/utils/RejectDialog";
import PrintService from "@/services/print_service";

export default defineComponent({
  components: {
    IonButtons,
    IonButton,
    IonIcon,
    IonModal,
    IonHeader,
    IonContent,
    IonToolbar,
    IonTitle,
    IonFooter,
    IonItem,
    IonLabel,
    IonList,
    IonPage,
  },
  data(): any {
    return {
      open: this.show as boolean,
      details: {
        client: {
          date_of_birth: "",
          first_name: "",
          id: "",
          last_name: "",
          middle_name: "",
          sex: "",
        },
      } as any,
      close: close,
      options: [
        { label: "Order Id", value: "-" },
        { label: "Tracking Number", value: "-" },
        { label: "Accession Number", value: "-" },
        { label: "Patient Name", value: "-" },
        { label: "Specimen Type", value: "-" },
        { label: "Test(s) Ordered", value: "-" },
        { label: "Specimen Status", value: "-" },
      ],
    };
  },

  props: {
    test: {
      required: true,
      type: Object as PropType<any>,
    },
    show: {
      required: true,
      default: false,
      type: Boolean as PropType<any>,
    },
  },
  async created() {
    const client = Client();
    const res = await client.getJson(`tests/${this.test.id}`);
    if (res?.ok) {
      this.details = res?.data;
    }
  },
  methods: {
    viewTestDetails(): void {
      this.handleDialog();
      router.push(`test/details?test_id=${this.test.id}`);
    },
    rejectOrder() {
      this.open = false;
      router.push(`test/reject/${this.test.id}`);
    },
    handleDialog(): void {
      this.open = false;
    },
    async changeSpecimenStatus(status: string): Promise<void> {
      const client = Client();

      const res = await client.putJson(`order_statuses/${status}`, {
        order_id: this.details.order_id,
      });

      if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`);

        router.go(0);

        this.handleDialog();
      }
    },
    loadOrder() {
      Orders().searchbyID(this.details.accession_number);
    },
    async addTest() {
      this.handleDialog();
      const client = Client();
      const patientSex = this.details.client?.sex.match(/f/i) ? "Female" : "Male";
      MultiStepPopupForm(
        [
          {
            id: "test_types",
            helpText: "Select test types",
            type: FieldType.TT_MULTIPLE_SELECT,
            config: {
              showKeyboard: true,
            },
            computedValue: (val: Option[], f: any) => {
              return {
                order_id: this.details.order_id,
                tests: val.map((v: { value: any; label: any }) => {
                  return {
                    specimen: v.value,
                    test_type: v.label,
                  };
                }),
              };
            },
            options: async () => {
              const res = await client.getJson("specimen/test_types", {
                specimen_id: this.details.specimen_id as number,
                sex: patientSex
              });

              if (!res?.ok) return [];
              return res.data.map((testType: any) => ({
                label: testType,
                value: this.details.specimen_id,
              }));
            },
            validation: (val: Option) => Validation.required(val),
          },
        ],
        async (_: any, payload: any) => {
          const req = await client.postJson(
            "orders/add_test_to_order",
            payload.test_types
          );
          if (!req?.ok) return toastWarning(req?.error);
          modalController.getTop().then((v) => v && modalController.dismiss());
          this.loadOrder();
        },
        null
      );
    },
    printAccessNumber() {
      const printService = new PrintService();

      const accession_number = this.details.accession_number;

      printService.writeLbl(
        "printout/accession_number",
        { accession_number },
        `${accession_number}.lbl`
      );
    },
  },
});
</script>
<style lang=""></style>
